.photu .special{
  display: inline-block;
  position: relative;
  width: 320px;
  height: 450px;
  background: #232323;
  box-shadow: 3px 3px 10px #6ecce9;
  border-radius: 20px;
  overflow: hidden;
}

.photu .special:before{
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #6de9e9;
  -webkit-clip-path: circle(150px at 80% 20%);
          clip-path: circle(150px at 80% 20%);
  transition: 0.5s ease-in-out;
}

.photu .special:hover:before{
  -webkit-clip-path: circle(300px at 80% -20%);
          clip-path: circle(300px at 80% -20%);
}

.photu .special:after{
  content: 'MECHABROS';
  position: absolute;
  top: 50%;
  left: 1%;
  font-size: 48px;
  font-weight: 800;
  font-style: italic;
  color: rgba(90, 225, 230, 0.05)
}

.photu .special .imgBx{
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  z-index: 10000;
  width: 100%;
  height: 220px;
  transition: 0.5s;
}

.photu .special:hover .imgBx{
  top: 0%;
  -webkit-transform: translateY(0%);
          transform: translateY(0%);
    
}

.photu .special .imgBx img{
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 270px;
}

.photu .special .contentBx{
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 100px;
  text-align: center;
  transition: 1s;
  z-index: 10;
}

.photu .special:hover .contentBx{
  height: 200px;
}

.photu .special .contentBx h2{
  position: relative;
  font-weight: 600;
  letter-spacing: 1px;
  color: #fff;
  margin: 20;
}


.photu .special .contentBx .size, .photu .special .contentBx .color {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 8px 20px;
  transition: 0.5s;opacity: 0;
  visibility: hidden;
  padding-top: 0;
  padding-bottom: 10px;
}

.photu .special:hover .contentBx .size{
  opacity: 1;
  visibility: visible;
  transition-delay: 0.5s;
}

.photu .special:hover .contentBx .color{
  opacity: 1;
  visibility: visible;
  transition-delay: 0.6s;
}

.photu .special .contentBx .size h3, .photu .special .contentBx .color h3{
  color: #fff;
  font-weight: 300;
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-right: 10px;
}

/* ATTRIBUTES CARDU */

.photu .special .contentBx .size span{
  width: 46px;
  height: 26px;
  text-align: center;
  line-height: 26px;
  font-size: 14px;
  display: inline-block;
  color: #111;
  background: #fff;
  margin: 0 5px;
  transition: 0.5s;
  color: #111;
  border-radius: 4px;
  cursor: pointer;
}

.photu .special .contentBx .size span:hover{
  background: #9bdc28;
}

.photu .special .contentBx .color span{
  width: 20px;
  height: 20px;
  background: #ff0;
  border-radius: 50%;
  margin: 0 5px;
  cursor: pointer;
}

.photu .special .contentBx .color span:nth-child(2){
  background: #9bdc28;
}

.photu .special .contentBx .color span:nth-child(3){
  background: #03a9f4;
}

.photu .special .contentBx .color span:nth-child(4){
  background: #e91e63;
}

.photu .special .contentBx a{
  display: inline-block;
  padding: 10px 20px;
  background: #fff;
  border-radius: 4px;
  margin-top: 10px;
  text-decoration: none;
  font-weight: 600;
  color: #111;
  opacity: 0;
  -webkit-transform: translateY(50px);
          transform: translateY(50px);
  transition: 0.5s;
  margin-top: 0;
}

.photu .special:hover .contentBx a{
  opacity: 1;
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
  transition-delay: 0.75s;
  
}


