/* COMING SOON */

.wrapper {
  position: relative;
  -webkit-perspective: 40em;
          perspective: 40em;
  display: grid;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

.pard {

  grid-area: 1 / 1;
  height: 100px;
  width: 190px;
  -webkit-transform: translateX(10px) rotateY(25deg) rotateX(10deg);
          transform: translateX(10px) rotateY(25deg) rotateX(10deg);
  background: rgb(118, 232, 240);
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 0px;
  color: #000;
  text-transform: uppercase;
  font-size: 60px;
  font-weight: 900;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  box-shadow: 0 10px 30px -3px rgba(0,0,0,.1);
  -webkit-filter:drop-shadow(5px 5px 5px rgb(117, 187, 219));
          filter:drop-shadow(5px 5px 5px rgb(117, 187, 219));
}

h5 {
  font-size: 24px;
