}

.pard .enclosed {
  background: #000;
  line-height: 1.5;
  color: rgb(118, 232, 240);
  padding: 0 5px;
  -webkit-transform: translate(-1px, 1px) scale(0.75);
          transform: translate(-1px, 1px) scale(0.75);
  -webkit-transform-origin: right center;
          transform-origin: right center;
}

.wrapper:before {
  --bw: 9px;
  grid-area: 1 / 1;
  content: '';
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  height: 100%;
  width: 100%;
  margin-top: calc(-1 * var(--bw));
  margin-left: calc(-1 * var(--bw));
  background: transparent;
  -webkit-transform: translateX(-60px) rotateY(-30deg) rotateX(15deg) scale(1.03);
          transform: translateX(-60px) rotateY(-30deg) rotateX(15deg) scale(1.03);
  pointer-events: none;
  border: var(--bw) solid #7eebf3;
  box-sizing: content-box;
  -webkit-filter:drop-shadow(5px 5px 5px rgb(117, 187, 219));
          filter:drop-shadow(5px 5px 5px rgb(117, 187, 219));
}


.wrapper:hover > div,
.wrapper:hover:before {
  -webkit-transform: none;
          transform: none;
}


.wrapper > div,
.wrapper:before {
  will-change: transform;
  transition: .3s transform cubic-bezier(.25,.46,.45,1);
}

